.menu {
  overflow: auto;
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  z-index: 2;

  &__top {
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &_phone-number {
    display: block;
    margin-bottom: 16px;
    color: $sidebar-text-color;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }
  &-book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $sidebar-text-color;
    color: $sidebar-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
